import {React, useState} from 'react'
import ListLayanan from './listlayanan'
import AjuBarantan from './ajubarantan'

const DataLayanan = () =>{
    const [page, setPage] = useState(0)
    return(
        <>
        {
            page === 0 ?
            <ListLayanan changePage={setPage}/>   
            :
            page === 1 ?
            <AjuBarantan changePage={setPage}/>
            :
            <>
            </>         
        }
        </>
    )
}

export default DataLayanan