import { CFormGroup, CInput, CLabel, CRow, CCol, CSelect, CTextarea, CButton } from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import AsyncSelect from 'react-select/async';
import Select from 'react-select'
import CreatableSelect from 'react-select/creatable';
import Swal from 'sweetalert2';
import supabase from '../../../supabase'

const Perusahaan = () =>{
    const [disabled, setDisabled] = useState(false)
    const dispatch = useDispatch()
    const datasupabase = useSelector((state)=>state.datasupabase)
    const [disabledpgjawab, setDisabledpgjawab] = useState(true)
    const [disabledpembeli, setDisabledpembeli] = useState(false)
    const [perusahaan, setPerusahaan] = useState({})
    const kirimPerusahaan = async () =>{
        let sementara = {...datasupabase.jsondata}
        sementara.perusahaan = perusahaan
        const { data, error } = await supabase
        .from('td_superset')
        .update({ jsondata: sementara })
        .eq('id', datasupabase.id)  
        if(!error){
            Swal.fire({
                icon: 'success',
                title: 'Sukses'
            })  
            dispatch({type:'set', datasupabase:data[0]})
        }          
    }
    useEffect(()=>{
        setPerusahaan({...(datasupabase.jsondata ? datasupabase.jsondata.perusahaan : {})})
    },[])
    const handleCheck = (e) =>{
        setDisabledpembeli(e.target.checked)
        setPerusahaan({
            ...perusahaan,
            [e.target.name]:e.target.checked,
            ['nmpembeli']:"",
            ['negarapembeli']:"",
            ['alamatpembeli']:""            
        })        
    }
    const handleInputChange = (e) =>{
        if(e.target.name === 'jnsidentitaseksportir'){
            if(e.target.value !== "1"){
                setDisabled(false)
                setPerusahaan({
                    ...perusahaan,
                    [e.target.name]:e.target.value,
                    ['nibeksportir']:"",
                    ['nmperusahaaneksportir']:"",
                    ['rtrweksportir']:"",
                    ['kecamataneksportir']:"",
                    ['kotakabeksportir']:"",
                    ['kodeposeksportir']:"",
                    ['alamateksportir']:"",
                    ['teleponeksportir']:""
                })
            } else {
                setDisabled(true)
                setPerusahaan({...perusahaan,[e.target.name]:e.target.value})
            }          
        } else {
            setPerusahaan({...perusahaan,[e.target.name]:e.target.value})
        }
    }
    const arr = [
        {'value':"012337556641000", "label":"012337556641000"}
    ]
    const arrPgjawab = [
        {
            jabatanpgjawab:"KOMISARIS",
            notelppgjawab:"0816503995",
            alamatpgjawab:"DARMO PERMAI TIMUR 4/62"
        },
        {
            jabatanpgjawab:"DIREKTUR",
            notelppgjawab:"08123251959",
            alamatpgjawab:"PANDUGO BARU XII/M-15"
        },
        {
            jabatanpgjawab:"BADAN HUKUM",
            notelppgjawab:"0811325051",
            alamatpgjawab:"JL. RAYA CANDI NO. 5"
        },
        {
            jabatanpgjawab:"BADAN HUKUM",
            notelppgjawab:"08123011667",
            alamatpgjawab:"JL. RAYA CANDI NO. 5"
        }                        
    ]
    const handleSelect = (e, index) =>{
        if(index.name === "ideksportir"){
            setPerusahaan({
                ...perusahaan,
                [index.name]:e,
                ['nibeksportir']:"8120310100981",
                ['nmperusahaaneksportir']:"UNICHEMCANDI INDONESIA",
                ['rtrweksportir']:"-",
                ['kecamataneksportir']:"",
                ['kotakabeksportir']:"KAB. SIDOARJO",
                ['kodeposeksportir']:"61271",
                ['alamateksportir']:"Jl. Raya Candi No. 5",
                ['teleponeksportir']:"0318912342"
            })            
        } else if(index.name === "npwpppjk") {
            setPerusahaan({
                ...perusahaan,
                [index.name]:e,
                ['nmppjk']:"UNICHEMCANDI INDONESIA",
                ['alamatppjk']:"Jl. Raya Candi No. 5"
            })
        } else if (index.name === "namapgjawab"){
            if(e.value == "5"){
                setDisabledpgjawab(false)
                setPerusahaan({
                    ...perusahaan,
                    [index.name]:e,
                    ['nmpgjawab']:"",
                    ['jabatanpgjawab']:"",
                    ['notelppgjawab']:"",
                    ['alamatpgjawab']:""                
                })                 
            } else {
                setPerusahaan({
                    ...perusahaan,
                    [index.name]:e,
                    ['nmpgjawab']:e,
                    ['jabatanpgjawab']:arrPgjawab[parseInt(e.value)-1].jabatanpgjawab,
                    ['notelppgjawab']:arrPgjawab[parseInt(e.value)-1].notelppgjawab,
                    ['alamatpgjawab']:arrPgjawab[parseInt(e.value)-1].alamatpgjawab
                })                
            }
        }
    }
    const handleInputSelect = (e) =>{
        if(e.length > 5){
            return new Promise(resolve => {
                setTimeout(() => {
                  resolve(arr);
                }, 1000);
              });            
        }
    }
    return(
        <>
        <CRow>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Jenis Pemberitahu</CLabel>
                    <Select
                    defaultValue={{ label: "PENGUSAHA", value: '1' }}
                    options={[
                        {label:'PENGUSAHA',value:'1'},
                        {label:'PPJK',value:'2'},
                        {label:'VENDOR',value:'3'},
                    ]}
                    />
                </CFormGroup>
            </CCol>
        </CRow>
        <CRow>
            <CCol md="6">
                <h5>Eksportir</h5>
                <hr/>
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Jenis Identitas</CLabel>
                            <CSelect name="jnsidentitaseksportir" value={perusahaan.jnsidentitaseksportir} onChange={(e)=>handleInputChange(e)} >
                            <option value="0">--Pilih--</option>
                            <option value="1">NPWP</option>
                            <option value="2">PASSPORT</option> 
                            <option value="3">LAINNYA</option>                                                           
                            </CSelect>
                        </CFormGroup>
                    </CCol>
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>No Identitas</CLabel>
                            {
                                perusahaan.jnsidentitaseksportir == '1' ?
                                <AsyncSelect name="ideksportir" value={perusahaan.ideksportir} onChange={(index, e)=>handleSelect(index, e)} loadOptions={(e)=>handleInputSelect(e)}/>
                                :
                                <CInput name="noidentitaseksportir" value={perusahaan.noidentitaseksportir} onChange={(e)=>handleInputChange(e)} placeholder="No Identitas Eksportir"/>
                            }
                        </CFormGroup>
                    </CCol>
                </CRow>  
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>NIB</CLabel>
                            <CInput value={perusahaan.nibeksportir} name="nibeksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="NIB Eksportir"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="8">
                        <CFormGroup>
                            <CLabel>Nama Perusahaan</CLabel>
                            <CInput value={perusahaan.nmperusahaaneksportir} name="nmperusahaaneksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Nama Perusahaan Eksportir"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>RT/RW</CLabel>
                            <CInput value={perusahaan.rtrweksportir} name="rtrweksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="RT/RW Eksportir"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Kecamatan</CLabel>
                            <CInput value={perusahaan.kecamataneksportir} name="kecamataneksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Kecamatan Eksportir"/>
                        </CFormGroup>
                    </CCol>                                        
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Kota/Kabupaten</CLabel>
                            <CInput value={perusahaan.kotakabeksportir} name="kotakabeksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Kota/Kabupaten Eksportir"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Kode Pos</CLabel>
                            <CInput value={perusahaan.kodeposeksportir} name="kodeposeksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Kodepos Eksportir"/>
                        </CFormGroup>
                    </CCol>                    
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea value={perusahaan.alamateksportir} name="alamateksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Alamat Eksportir"/>
                        </CFormGroup>
                    </CCol>
                </CRow>                                
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Status</CLabel>
                            <CSelect value={perusahaan.statuseksportir} name="statuseksportir" onChange={(e)=>handleInputChange(e)}>
                                <option value="">--Pilih--</option>
                                <option value="1">Koperasi</option>
                                <option value="2">PMDN (Migas)</option>
                                <option value="3">PMDN (Non Migas)</option>
                                <option value="4">PMA (Migas)</option>
                                <option value="5">PMA (Non Migas)</option>  
                                <option value="6">BUMN</option>
                                <option value="7">BUMD</option>
                                <option value="8">Perorangan</option>
                                <option value="9">Usaha Mikro, Kecil, dan Menengah</option>   
                                <option value="10">Lainnya</option>                                                                                                
                            </CSelect>                            
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nomor Identitas BU/PU</CLabel>
                            <CInput value={perusahaan.idbupu} name="idbupu" onChange={(e)=>handleInputChange(e)} placeholder="No Identitas BU/PU Eksportir"/>
                        </CFormGroup>
                    </CCol>
                </CRow>  
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Telepon</CLabel>
                            <CInput value={perusahaan.teleponeksportir} name="teleponeksportir" onChange={(e)=>handleInputChange(e)} disabled={perusahaan.jnsidentitaseksportir == '1'} placeholder="Telepon Eksportir"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Email</CLabel>
                            <CInput value={perusahaan.emaileksportir} name="emaileksportir" onChange={(e)=>handleInputChange(e)} placeholder="Email Eksportir"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>                 
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>On Behalf</CLabel>
                            <CInput value={perusahaan.onbehalfeksportir} name="onbehalfeksportir" onChange={(e)=>handleInputChange(e)} placeholder="On Behalf"/>
                        </CFormGroup>
                    </CCol>
                </CRow>                                                                                           
            </CCol>
            <CCol md="6">
                <h5>Penanggung Jawab</h5>
                <hr/>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <div className="d-flex flex-row justify-content-between">
                            <CLabel>Nama Penanggung Jawab</CLabel>
                            {
                                !disabledpgjawab ? 
                                <div role="button" onClick={()=>setDisabledpgjawab(!disabledpgjawab)} ><i class="fas fa-undo"></i></div>
                                :
                                <></>
                            }
                            </div>
                            {
                                !disabledpgjawab ? 
                                <CInput value={perusahaan.nmpgjawab} name="nmpgjawab" onChange={(e)=>handleInputChange(e)} placeholder="Nama Penanggung Jawab"/>
                                :
                                <CreatableSelect 
                                value={perusahaan.nmpgjawab}
                                name="namapgjawab"
                                onChange={(index, e)=>handleSelect(index, e)}
                                options={[
                                    {value:'1', label:'GO, BOSSE GOZALI'},
                                    {value:'2', label:'RAHARJO'},
                                    {value:'3', label:'PT. ENERGI HARAPAN TERPADU'},
                                    {value:'4', label:'PT. HARRISON CANDI INDONESIA'},  
                                    {value:'5', label:'Buat Baru..'},                              
                                ]} />                                
                            }
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Jabatan</CLabel>
                            <CInput value={perusahaan.jabatanpgjawab} name="jabatanpgjawab" onChange={(e)=>handleInputChange(e)} disabled={disabledpgjawab} placeholder="Jabatan Penanggung Jawab"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Telepon</CLabel>
                            <CInput value={perusahaan.notelppgjawab} name="notelppgjawab" onChange={(e)=>handleInputChange(e)} disabled={disabledpgjawab} placeholder="Telepon Penanggung Jawab"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea value={perusahaan.alamatpgjawab} name="alamatpgjawab" onChange={(e)=>handleInputChange(e)} disabled={disabledpgjawab} placeholder="Alamat Penanggung Jawab"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>
                <h5>PPJK</h5>
                <hr/>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>NPWP</CLabel>
                            <AsyncSelect value={perusahaan.npwpppjk} name="npwpppjk" onChange={(index, e)=>handleSelect(index, e)} loadOptions={(e)=>handleInputSelect(e)}/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nama PPJK</CLabel>
                            <CInput value={perusahaan.nmppjk} name="nmppjk" disabled placeholder="Nama PPJK"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea value={perusahaan.alamatppjk} name="alamatppjk" disabled placeholder="Alamat PPJK"/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>                                                                                                                                           
            </CCol>                       
        </CRow>
        <hr/>
        <CRow>
            <CCol md="6">
            <div className="d-flex flex-row justify-content-between">
                <h5>Penerima</h5>
            </div>
                <hr/>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nama</CLabel>
                            <CInput 
                            name="nmpenerima"
                            value={perusahaan.nmpenerima}
                            onChange={(e)=>handleInputChange(e)}                            
                            placeholder="Nama Penerima"/>
                        </CFormGroup>
                    </CCol> 
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>To Order</CLabel>
                            <CInput 
                            name="toorder" 
                            value={perusahaan.toorder}
                            onChange={(e)=>handleInputChange(e)}                            
                            placeholder="To Order"/>
                        </CFormGroup>
                    </CCol>                                      
                </CRow>  
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Negara</CLabel>
                            <CSelect 
                            name="negarapenerima" 
                            value={perusahaan.negarapenerima}
                            onChange={(e)=>handleInputChange(e)}                            
                            placeholder="Negara Penerima"/>
                        </CFormGroup>
                    </CCol>   
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea 
                            name="alamatpenerima" 
                            value={perusahaan.alamatpenerima}
                            onChange={(e)=>handleInputChange(e)}                            
                            placeholder="Alamat Penerima"/>
                        </CFormGroup>
                    </CCol>                                        
                </CRow>                 
            </CCol>
            <CCol md="6">
            <div className="d-flex flex-row justify-content-between">
                <h5>Pembeli</h5>
                <div><input checked={perusahaan.pembelisamadgpenerima} name="pembelisamadgpenerima" onChange={(e)=>handleCheck(e)} type="checkbox"/> Sama dengan penerima</div>
            </div>
                <hr/>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Nama</CLabel>
                            <CInput value={
                                perusahaan.pembelisamadgpenerima ? 
                                perusahaan.nmpenerima
                                :
                                perusahaan.nmpembeli
                            }
                            name="nmpembeli" 
                            onChange={(e)=>handleInputChange(e)}
                            disabled={perusahaan.pembelisamadgpenerima} placeholder="Nama Pembeli"/>
                        </CFormGroup>
                    </CCol>                                    
                </CRow>  
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Negara</CLabel>
                            <CSelect 
                            value={
                                perusahaan.pembelisamadgpenerima ? 
                                perusahaan.negarapenerima
                                :
                                perusahaan.negarapembeli
                            }
                            name="negarapembeli" 
                            onChange={(e)=>handleInputChange(e)}                            
                            disabled={perusahaan.pembelisamadgpenerima} placeholder="Negara Pembeli"/>
                        </CFormGroup>
                    </CCol>   
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea 
                            value={
                                perusahaan.pembelisamadgpenerima ? 
                                perusahaan.alamatpenerima
                                :
                                perusahaan.alamatpembeli
                            }
                            name="alamatpembeli" 
                            onChange={(e)=>handleInputChange(e)}                            
                            disabled={perusahaan.pembelisamadgpenerima} placeholder="Alamat Pembeli"/>
                        </CFormGroup>
                    </CCol>                                        
                </CRow>                                    
            </CCol>            
        </CRow>
        <hr/>
        <div className="d-flex flex-row justify-content-end">
            <div className="mr-2">
                <CButton onClick={()=>kirimPerusahaan()} style={{borderRadius:'12px'}} className="btn btn-success">Simpan</CButton>
            </div>           
        </div>
        </>
    )
}

export default Perusahaan