import { CButton, CFormGroup, CInput, CLabel, CRow, CCol, CTextarea, CForm, CDataTable, CModal, CModalHeader, CModalBody, CModalFooter } from '@coreui/react'
import {React, useState} from 'react'
import { useDispatch } from 'react-redux'
import Select from 'react-select'

const AjuBarantan = (props) =>{
    const [page, setPage] = useState(1)
    const [pageBarang, setPageBarang] = useState(1)
    const [modal, setModal] = useState(false)
    const dispatch = useDispatch()
    return(
        <>
        <div className="d-flex flex-row justify-content-center">
            <CButton onClick={()=>{
                props.changePage(0)
                dispatch({type:'set',tabtrue:true})                
            }} style={{borderRadius:'12px'}} className="btn btn-sm btn-light mr-auto"><i class="fas fa-chevron-left"></i> Kembali</CButton>
        </div>
        <div className="d-flex flex-row justify-content-start mt-2">
            <div 
            onClick={()=>setPage(1)}
            role="button" className="mr-5" style={
                page == "1" ?
                {
                    fontWeight:'bold',
                    color:'#02275d'
                }
                :
                {
                    fontWeight:'normal',
                    color:'#02275d'
                }                        
            }
            >
                Perusahaan
            </div>
            <div 
            onClick={()=>setPage(2)}
            role="button" className="mr-5" style={
                page == "2" ?
                {
                    fontWeight:'bold',
                    color:'#02275d'
                }
                :
                {
                    fontWeight:'normal',
                    color:'#02275d'
                }                        
            }            
            >
                Transportasi
            </div>
            <div
            onClick={()=>setPage(3)}
            role="button" className="mr-5" style={
                page == "3" ?
                {
                    fontWeight:'bold',
                    color:'#02275d'
                }
                :
                {
                    fontWeight:'normal',
                    color:'#02275d'
                }                        
            }            
            >
                Barang
            </div> 
            <div 
            onClick={()=>setPage(4)}
            role="button" className="mr-5" style={
                page == "4" ?
                {
                    fontWeight:'bold',
                    color:'#02275d'
                }
                :
                {
                    fontWeight:'normal',
                    color:'#02275d'
                }                        
            }            
            >
                Dokumen
            </div> 
            <div
            onClick={()=>setPage(5)}
            role="button" className="mr-5" style={
                page == "5" ?
                {
                    fontWeight:'bold',
                    color:'#02275d'
                }
                :
                {
                    fontWeight:'normal',
                    color:'#02275d'
                }                        
            }            
            >
                Checkpoint
            </div>                                                
        </div>  
        <div className="card px-3 py-3 mt-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            {
                page === 1 ?
                <>
                    <CRow>
                        <CCol md="6">
                            <CFormGroup>
                                <CLabel>Nomor Pengajuan</CLabel>
                                <CInput value="Nomor Pengajuan" disabled/>
                            </CFormGroup>
                        </CCol> 
                        <CCol md="6">
                            <CFormGroup>
                                <CLabel>Tanggal Pengajuan</CLabel>
                                <CInput value="Tanggal Pengajuan" disabled/>
                            </CFormGroup>
                        </CCol>                                               
                    </CRow> 
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>UPT</CLabel>
                                <Select/>
                            </CFormGroup>
                        </CCol> 
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Jenis Komoditas</CLabel>
                                <Select options={[
                                    {label:'Hewan', value:'1'},
                                    {label:'Tumbuhan', value:'2'},
                                ]}/>
                            </CFormGroup>
                        </CCol>   
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Peruntukan</CLabel>
                                <Select options={[
                                    {label:'Diperdagangkan', value:'1'},
                                    {label:'Konsumsi', value:'2'},
                                    {label:'Lainnya', value:'3'}
                                ]}/>
                            </CFormGroup>
                        </CCol>                                                                       
                    </CRow> 
                    <CRow>
                        <CCol md="6">
                            <h5>Eksportir</h5>
                            <hr/>                            
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Jenis Identitas</CLabel>
                                        <CInput value="Jenis Identitas" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Nomor Identitas</CLabel>
                                        <CInput value="Nomor Identitas" disabled/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow>
                            <CRow>
                                <CCol md="12">
                                    <CFormGroup>
                                        <CLabel>Nama</CLabel>
                                        <CInput value="Nama Eksportir" disabled/>
                                    </CFormGroup>
                                </CCol>                               
                            </CRow> 
                            <CRow>
                                <CCol md="12">
                                    <CFormGroup>
                                        <CLabel>Alamat</CLabel>
                                        <CTextarea value="Alamat Eksportir" disabled/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow>                             
                            <CRow>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Nomor Telepon</CLabel>
                                        <CInput value="Nomor Telepon" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Nomor Fax</CLabel>
                                        <CInput value="Nomor Fax" disabled/>
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Negara</CLabel>
                                        <Select value="Negara" disabled/>
                                    </CFormGroup>
                                </CCol>                                                                
                            </CRow>                                                                                
                            <h5>Penanggung Jawab</h5>
                            <hr/>
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Nama</CLabel>
                                        <CInput value="Nama Penanggung Jawab" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>No Telp</CLabel>
                                        <CInput value="Nomor Telp" disabled/>
                                    </CFormGroup>
                                </CCol>                                                                
                            </CRow>  
                            <CRow>
                                <CCol md="12">
                                    <CFormGroup>
                                        <CLabel>Alamat</CLabel>
                                        <CTextarea value="Alamat" disabled/>
                                    </CFormGroup>
                                </CCol>                                                            
                            </CRow>                           
                        </CCol>
                        <CCol md="6">
                            <h5>Pemasok</h5>
                            <hr/>                            
                            <CRow>
                                <CCol md="9">
                                    <CFormGroup>
                                        <CLabel>Nama</CLabel>
                                        <CInput placeholder="Nama Pemasok"/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="3">
                                    <CFormGroup>
                                        <CLabel>Negara</CLabel>
                                        <Select/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow>
                            <CRow>
                                <CCol md="12">
                                    <CFormGroup>
                                        <CLabel>Alamat</CLabel>
                                        <CTextarea placeholder="Alamat" />
                                    </CFormGroup>
                                </CCol>
                            </CRow>                            
                            <h5>Penerima</h5>
                            <hr/>                            
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Nama</CLabel>
                                        <CInput value="Nama Penerima" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Alamat</CLabel>
                                        <CTextarea value="Alamat" disabled/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow>  
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Kecamatan</CLabel>
                                        <CInput value="Kecamatan" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Kota/Kab</CLabel>
                                        <CInput value="Kota/Kab" disabled/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow> 
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Provinsi</CLabel>
                                        <CInput value="Provinsi" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Kota/Kab</CLabel>
                                        <CInput value="Kota/Kab" disabled/>
                                    </CFormGroup>
                                </CCol>                                
                            </CRow>
                            <CRow>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>No. Telp</CLabel>
                                        <CInput value="No Telp" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>No. Fax</CLabel>
                                        <CInput value="No Fax" disabled/>
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Negara</CLabel>
                                        <CInput value="Negara" disabled/>
                                    </CFormGroup>
                                </CCol>                                                                
                            </CRow>                                                                                                                  
                        </CCol>                        
                    </CRow> 
                    <CRow>
                        <CCol md="12">
                            <h5>Penandatangan Dokumen PPK</h5>
                            <hr/>  
                            <CRow>
                                <CCol md="2">
                                    <CFormGroup>
                                        <CLabel>Jenis Identitas</CLabel>
                                        <Select/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="3">
                                    <CFormGroup>
                                        <CLabel>Nomor Identitas</CLabel>
                                        <CInput/>
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Nama</CLabel>
                                        <CInput/>
                                    </CFormGroup>
                                </CCol>  
                                <CCol md="3">
                                    <CFormGroup>
                                        <CLabel>Jabatan</CLabel>
                                        <CInput/>
                                    </CFormGroup>
                                </CCol>                                                                                                
                            </CRow>                          
                        </CCol>
                    </CRow>                                      
                </>
                :
                page === 2 ?
                <>
                <h5>Sarana Pengangkut</h5>
                <hr/>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Cara Pengangkutan</CLabel>
                            <CInput value="Cara Pengangkutan" disabled/>                            
                        </CFormGroup>
                    </CCol>                   
                    <CCol md="8">
                        <CFormGroup>
                            <CLabel>Nama Sarana Pengangkut</CLabel>
                            <CInput value="Nama Sarana Angkut" disabled/>
                        </CFormGroup>
                    </CCol>                                          
                </CRow>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel><i>Call Sign</i></CLabel>
                            <CInput value="Call Sign" disabled/> 
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel><i>Bendera</i></CLabel>
                            <CInput value="Bendera" disabled/>                            
                        </CFormGroup>
                    </CCol>  
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Nomor Voy / Flight</CLabel>
                            <CInput value="Nomor Voy / Flight" disabled/>                            
                        </CFormGroup>
                    </CCol>                                        
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Perkiraan Tanggal Berangkat</CLabel>
                            <CInput value="Perkiraan Tanggal Berangkat" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel><i>Perkiraan Tanggal Tiba</i></CLabel>
                            <CInput value="Perkiraan Tanggal Tiba" disabled/>                            
                        </CFormGroup>
                    </CCol>                     
                </CRow>                               
                <h5>Sarana Pengangkut Transit</h5>
                <hr/>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Cara Pengangkutan Transit</CLabel>
                            <CInput value="Cara Pengangkutan Transit" disabled/>                            
                        </CFormGroup>
                    </CCol>                   
                    <CCol md="8">
                        <CFormGroup>
                            <CLabel>Nama Sarana Pengangkut Transit</CLabel>
                            <CInput value="Nama Sarana Pengangkut Transit" disabled/>
                        </CFormGroup>
                    </CCol>                                          
                </CRow>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel><i>Call Sign Transit</i></CLabel>
                            <CInput value="Call Sign Transit" disabled/> 
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel><i>Bendera Transit</i></CLabel>
                            <CInput value="Bendera Transit" disabled/>                            
                        </CFormGroup>
                    </CCol>  
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Nomor Voy / Flight Transit</CLabel>
                            <CInput value="Nomor Voy / Flight Transit" disabled/>                            
                        </CFormGroup>
                    </CCol>                                        
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Perkiraan Tanggal Berangkat Transit</CLabel>
                            <CInput value="Perkiraan Tanggal Berangkat Transit" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel><i>Perkiraan Tanggal Tiba Transit</i></CLabel>
                            <CInput value="Perkiraan Tanggal Tiba Transit" disabled/>                            
                        </CFormGroup>
                    </CCol>                     
                </CRow> 
                    <CRow>
                        <CCol md="6">
                            <h5>Kepelabuhanan</h5>
                            <hr/>
                            <CRow>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Pelabuhan Asal</CLabel>
                                        <CInput value="Pel Asal" disabled/>
                                    </CFormGroup>
                                </CCol>  
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Pelabuhan Transit</CLabel>
                                        <CInput value="Pel Transit" disabled/>                                        
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Pelabuhan Tujuan</CLabel>
                                        <CInput value="Pel Tujuan" disabled/>                                        
                                    </CFormGroup>
                                </CCol>                                                                                                              
                            </CRow>
                            <CRow>
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Negara Asal</CLabel>
                                        <CInput value="Neg Asal" disabled/>
                                    </CFormGroup>
                                </CCol>  
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Negara Transit</CLabel>
                                        <CInput value="Neg Transit" disabled/>                                        
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="4">
                                    <CFormGroup>
                                        <CLabel>Negara Tujuan</CLabel>
                                        <CInput value="Neg Tujuan" disabled/>                                        
                                    </CFormGroup>
                                </CCol>                                                                                                              
                            </CRow> 
                            <CRow>  
                                <CCol md="12">
                                        <CFormGroup>
                                            <CLabel>Tempat Penimbunan</CLabel>
                                            <CInput value="Tempat Penimbunan" disabled/>                                        
                                        </CFormGroup>
                                </CCol>                                                                                                              
                            </CRow>                          
                        </CCol>
                        <CCol md="6">
                            <h5>Lokasi Pemeriksaan</h5>
                            <hr/>
                            <CRow>
                                <CCol md="3">
                                    <CFormGroup>
                                        <CLabel>Lok. Pemeriksaan</CLabel>
                                        <CInput value="Lok. Pemeriksaan" disabled/>
                                    </CFormGroup>
                                </CCol> 
                                <CCol md="9">
                                    <CFormGroup>
                                        <CLabel>Alamat Pemeriksaan</CLabel>
                                        <CTextarea value="Alamat Pemeriksaan" disabled/>
                                    </CFormGroup>
                                </CCol>                                                    
                            </CRow>
                            <CRow>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Tanggal Periksa</CLabel>
                                        <CInput value="Tanggal Periksa" disabled/>
                                    </CFormGroup>
                                </CCol>
                                <CCol md="6">
                                    <CFormGroup>
                                        <CLabel>Tanggal Tiba</CLabel>
                                        <CInput value="Tanggal Tiba" disabled/>
                                    </CFormGroup>
                                </CCol>                    
                            </CRow>                                                            
                        </CCol>
                    </CRow>  
                    <h5>Instalasi Karantina</h5>
                    <hr/>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Nama Instalasi Karantina</CLabel>
                                    <CInput/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Alamat Instalasi Karantina</CLabel>
                                    <CTextarea/>
                                </CFormGroup>
                            </CCol>                    
                        </CRow>                                                                                 
                </>
                :
                page === 3 ?
                <>
                <div className="d-flex flex-row">
                    <div 
                    onClick={()=>setPageBarang(1)}
                    role="button" className="mr-3" style={
                        pageBarang == "1" ?
                        {
                            fontWeight:'bold',
                            color:'#02275d'
                        }
                        :
                        {
                            fontWeight:'normal',
                            color:'#02275d'
                        }                        
                    } 
                    >
                        Barang
                    </div>
                    <div
                    onClick={()=>setPageBarang(2)}
                    role="button" className="mr-3" style={
                        pageBarang == "2" ?
                        {
                            fontWeight:'bold',
                            color:'#02275d'
                        }
                        :
                        {
                            fontWeight:'normal',
                            color:'#02275d'
                        }                        
                    }                     
                    >
                        Kontainer
                    </div>
                    <div
                    onClick={()=>setPageBarang(3)}
                    role="button" className="mr-3" style={
                        pageBarang == "3" ?
                        {
                            fontWeight:'bold',
                            color:'#02275d'
                        }
                        :
                        {
                            fontWeight:'normal',
                            color:'#02275d'
                        }                        
                    }                     
                    >
                        Kemasan
                    </div>                                        
                </div>
                <div className="card px-3 py-3 mt-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    {
                        pageBarang === 1 ?
                        <>
                            <div>
                                <CRow>
                                    <CCol md="4">
                                        <CFormGroup>
                                            <CLabel>Nilai (Sesuai PEB)</CLabel>
                                            <CInput disabled/>
                                        </CFormGroup>
                                    </CCol>                
                                    <CCol md="3">
                                        <CFormGroup>
                                            <CLabel>Bruto</CLabel>
                                            <CInput disabled/>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol md="1">
                                        <CFormGroup>
                                            <CLabel>Satuan</CLabel>
                                            <CInput disabled/>
                                        </CFormGroup>
                                    </CCol>  
                                    <CCol md="3">
                                        <CFormGroup>
                                            <CLabel>Netto</CLabel>
                                            <CInput disabled/>
                                        </CFormGroup>
                                    </CCol>
                                    <CCol md="1">
                                        <CFormGroup>
                                            <CLabel>Satuan</CLabel>
                                            <CInput disabled/>
                                        </CFormGroup>
                                    </CCol>                                
                                </CRow>                        
                            </div>                         
                            <CDataTable
                            addTableClasses="joss"
                            fields={[
                                {label:'No',key:'no'},
                                {label:'Kode HS',key:'kdhs'},
                                {label:'Uraian',key:'uraian'},
                                {label:'Negara Asal',key:'negasal'},
                                {label:'Action',key:'action'},
                            ]}
                            items={[
                                {
                                    kdhs:'0203190000',
                                    uraian:'[Hewan]--Lain-lain',
                                    negasal:'Indonesia'
                                }
                            ]}
                            scopedSlots={{
                                'no':
                                (item, index)=>{
                                    return(
                                        <td>
                                            {index+1}
                                        </td>
                                    )
                                },
                                'action':
                                (item, index)=>{
                                    return(
                                        <td>
                                            <div className="d-flex flex-row">
                                                <div className="mr-2">
                                                    <CButton className="btn btn-success btn-sm"><i class="fas fa-pencil-alt"></i></CButton>
                                                </div>                                             
                                            </div>
                                        </td>
                                    )
                                },                                
                            }}
                            />                        
                        </> 
                        :
                        pageBarang === 2 ?
                        <>
                            <CDataTable
                            addTableClasses="joss"
                            fields={[
                                {label:'No',key:'no'},
                                {label:'No Kontainer',key:'nokontainer'},
                                {label:'Ukuran Kontainer',key:'ukkontainer'},
                                {label:'Status Kontainer',key:'statuskontainer'},
                                {label:'Nomor Seal',key:'nosealkontainer'},
                                {label:'Tipe Kontainer',key:'tipekontainer'},
                                {label:'Action',key:'action'}
                            ]}
                            items={[
                                {
                                    nokontainer:'No Kontainer',
                                    ukkontainer:'20',
                                    statuskontainer:'FCL',
                                    nosealkontainer:'No Seal Kontainer',
                                    tipekontainer:'Tunnel Type Container'
                                }
                            ]}
                            scopedSlots={{
                                'no':
                                (item, index)=>{
                                    return(
                                        <td>
                                            {index+1}
                                        </td>
                                    )
                                },
                                'action':
                                (item, index)=>{
                                    return(
                                        <td>
                                            <div className="d-flex flex-row">
                                                <div>
                                                    <CButton className="btn btn-success btn-sm"><i class="far fa-eye"></i></CButton>
                                                </div>
                                            </div>
                                        </td>
                                    )
                                }
                            }}
                            />                       
                        </>     
                        : 
                        pageBarang === 3 ?
                        <>
                            <CDataTable
                            addTableClasses="joss"
                            fields={[
                                {label:'No',key:'no'},
                                {label:'Kode Kemasan',key:'kdKemasan'},
                                {label:'Nama Kemasan',key:'nmKemasan'},
                                {label:'Merek Kemasan',key:'merekKemasan'},
                                {label:'Jumlah Kemasan',key:'jmlKemasan'},
                                {label:'Action',key:'action'}
                            ]}
                            items={[
                                {
                                    kdKemasan:'DM',
                                    nmKemasan:'Crate, bulk, wooden',
                                    merekKemasan:'Merek Kemasan',
                                    jmlKemasan:'Jumlah Kemasan',
                                }
                            ]}
                            scopedSlots={{
                                'no':
                                (item, index)=>{
                                    return(
                                        <td>
                                            {index+1}
                                        </td>
                                    )
                                },
                                'action':
                                (item, index)=>{
                                    return(
                                        <td>
                                            <div className="d-flex flex-row">
                                                <div>
                                                    <CButton className="btn btn-success btn-sm"><i class="far fa-eye"></i></CButton>
                                                </div>
                                            </div>
                                        </td>
                                    )
                                }
                            }}                            
                            />                          
                        </>     
                        :
                        <>
                        </>                       
                    }
                </div>
                </>
                :
                page === 4 ?
                <>
                <div className="d-flex flex-row justify-content-end mb-3">
                    <CButton onClick={()=>setModal(true)} className="btn" style={{borderRadius:'12px', backgroundColor:'#02275d', color:'white'}}><i class="fas fa-plus"></i></CButton>
                </div>
                <CModal
                show={modal}
                onClose={()=>setModal(false)}
                size="lg"
                >
                    <CModalHeader
                    closeButton
                    ><h5>Tambah Dokumen</h5></CModalHeader>
                    <CModalBody>
                        <CRow>
                            <CCol md="5">
                                <CFormGroup>
                                    <CLabel>Pilih Dokumen</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Nomor Dokumen</CLabel>
                                    <CInput/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Tanggal Dokumen</CLabel>
                                    <CInput type="date"/>
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Negara Asal</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Kota</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>
                        </CRow>   
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Keterangan</CLabel>
                                    <CTextarea/>
                                </CFormGroup>
                            </CCol>
                        </CRow> 
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <input type="file"></input>
                                </CFormGroup>
                            </CCol>
                        </CRow>                                            
                    </CModalBody>
                    <CModalFooter>
                        <div className="d-flex flex-row justify-content-end">
                            <div className="mr-2">
                                <CButton onClick={()=>{
                                    setModal(false)                                    
                                }} className="btn btn-success">Simpan</CButton>
                            </div>
                            <div>
                                <CButton onClick={()=>{
                                    setModal(false)                                     
                                }} className="btn btn-danger">Batal</CButton>
                            </div>                            
                        </div>
                    </CModalFooter>
                </CModal>
                <CDataTable
                addTableClasses="joss"
                fields={[
                    {label:'No', key:'no'},
                    {label:'Kode Dokumen', key:'kddok'},
                    {label:'Nama Dokumen', key:'kddok'},
                    {label:'Nomor Dokumen', key:'kddok'},
                    {label:'Tanggal Dokumen', key:'kddok'},
                    {label:'Action', key:'action'},
                ]}
                />                
                </>
                :                
                <>
                <div className="d-flex flex-row justify-content-center">
                    <div className="d-flex flex-row mr-3">
                        <div className="d-flex flex-row card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex align-items-center mr-2">
                            <i style={{color:'green'}} class="fas fa-check"></i>
                        </div>
                        <div className="d-flex">
                            Data Perusahaan                            
                        </div>
                        </div>
                    </div>
                    <div className="d-flex flex-row mr-3">
                        <div className="d-flex flex-row card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex align-items-center mr-2">
                            <i style={{color:'green'}} class="fas fa-check"></i>
                        </div>
                        <div className="d-flex">
                            Data Transportasi                            
                        </div>
                        </div>
                    </div>
                    <div className="d-flex flex-row mr-3">
                        <div className="d-flex flex-row card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex align-items-center mr-2">
                            <i style={{color:'green'}} class="fas fa-check"></i>
                        </div>
                        <div className="d-flex">
                            Data Barang                            
                        </div>
                        </div>
                    </div> 
                    <div className="d-flex flex-row">
                        <div className="d-flex flex-row card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex align-items-center mr-2">
                            <i style={{color:'green'}} class="fas fa-check"></i>
                        </div>
                        <div className="d-flex">
                            Data Dokumen                            
                        </div>
                        </div>
                    </div>                                                           
                </div>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <div><b>Disclaimer</b></div>
                    Saya dengan ini menyatakan bahwa data yang saya sampaikan dalam pengajuan ini adalah benar dan saya bertanggung jawab apabila terjadi kesalahan di kemudian hari
                </div>
                <div className="d-flex flex-row ml-3">
                    <div className="mr-3">
                        <input type="checkbox"/>                    
                    </div>
                    <div>
                        Saya Menyetujui                        
                    </div>
                </div>
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2">
                        <CButton onClick={()=>{
                            dispatch({type:'set',tabtrue:true}) 
                            props.changePage(0)}} style={{borderRadius:'12px'}} className="btn btn-success">Kirim</CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>{
                            dispatch({type:'set',tabtrue:true}) 
                            props.changePage(0)}} style={{borderRadius:'12px'}} className="btn btn-light">Simpan</CButton>
                    </div>                    
                </div>
                </>
            }
        </div>      
        </>
    )
}

export default AjuBarantan