import {React} from 'react'
import {CButton, CDataTable} from '@coreui/react'
import Swal from 'sweetalert2'
import { useDispatch, useSelector } from 'react-redux'

const ListLayanan = (props) =>{
    const dispatch = useDispatch()
    const nella = useSelector((state)=>state.nella)
    return(
        <>
        {/* <div style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}> */}
            <div className="card px-3 py-3 d-flex flex-row justify-content-between align-items-center mb-2" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <CDataTable
        addTableClasses='joss'
        fields={[
            {label:'Layanan', key:'1'},
            {label:'Nomor Pengajuan', key:'2'},
            {label:'Nomor Pendaftaran', key:'3'},
            {label:'Tanggal Pendaftaran', key:'4'},
            {label:'Status', key:'5'},
            {label:'Action', key:'6'},
        ]}
        items={nella== 'A' ? [
            {
                1:'Pabean dan/atau Karantina',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            },
            {
                1:'Pabean dan/atau Karantina dan SKA',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            }          
        ] : [
            {
                1:'Pabean dan/atau Karantina',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            }         
        ]}
        scopedSlots={{
            '6':
            (item, index)=>{
                return(
                    <td>
                        <div className="d-flex flex-row justify-content-start mb-2">
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-arrow-right"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-pencil-alt"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#f86c6b', color:'#fff', borderRadius:'12px'}}><i class="fas fa-ban"></i></CButton>                                
                            </div>                                                        
                        </div>                        
                    </td>
                )
            }
        }}                
                />
                {/* <div><h5>Pengajuan Pabean dan/atau Karantina</h5></div>
                <div>
                <CButton className="btn btn-sm"><i class="fas fa-chevron-down"></i> Status</CButton>
                <CButton onClick={()=>{
                    Swal.fire({
                        icon: <i class="fas fa-angle-right"></i>,
                        text:'to be added'
                    })                    
                }} className="btn btn-light btn-sm" style={{backgroundColor:'#02275d', color:'white'}}><i class="fas fa-angle-right"></i></CButton></div> */}
            </div>
            {/* <div className="card px-3 py-3 d-flex flex-row justify-content-between align-items-center mb-2" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div><h5>Pengajuan Barantan</h5></div>
                <div>
                <CButton className="btn btn-sm"><i class="fas fa-chevron-down"></i> Status</CButton>
                <CButton onClick={()=>{
                    props.changePage(1)
                    dispatch({type:'set', tabtrue:false})                    
                }} className="btn btn-light btn-sm" style={{backgroundColor:'#02275d', color:'white'}}><i class="fas fa-angle-right"></i></CButton></div>
            </div> */}
            {/* <div className="card px-3 py-3 d-flex flex-row justify-content-between align-items-center mb-2" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div><h5>Pengajuan BKIPM</h5></div>
                <div>
                <CButton className="btn btn-sm"><i class="fas fa-chevron-down"></i> Status</CButton>
                <CButton onClick={()=>{
                    Swal.fire({
                        icon: <i class="fas fa-angle-right"></i>,
                        text:'To be Added Later'
                    })                      
                }} className="btn btn-light btn-sm" style={{backgroundColor:'#02275d', color:'white'}}><i class="fas fa-angle-right"></i></CButton></div>
            </div>  */}
            {/* {
                nella == 'A' ?
                <div className="card px-3 py-3 d-flex flex-row justify-content-between align-items-center mb-2" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <div><h5>Pengajuan SKA</h5></div>
                    <div>
                    <CButton className="btn btn-sm"><i class="fas fa-chevron-down"></i> Status</CButton>
                    <CButton onClick={()=>{
                        Swal.fire({
                            icon: <i class="fas fa-angle-right"></i>,
                            text:'To be Added'
                        })                      
                    }} className="btn btn-light btn-sm" style={{backgroundColor:'#02275d', color:'white'}}><i class="fas fa-angle-right"></i></CButton></div>
                </div>  
                :
                <>
                </>                
            }                                   */}
        {/* </div> */}
        </>        
    )
}

export default ListLayanan